<template lang="pug">
  .home(:class="{'bg-dark' : showRightContent === true}")
    .container-fluid
      transition(name="slide-fade")
        .row.justify-content-between.shortRight(v-if="showRightContent === false")
          .col-md-8.px-0
            leftContent
          .col-md-4.px-0
            rightContent
            .showAllSideBar(@click="showRightContentEvent")
              .bar
      transition(name="slide-fade")
        .row.justify-content-between.lognRight(v-if="showRightContent === true")
          .col-md-1
          .col-md-11.bg-light.px-0
            .showAllSideBar(@click="showRightContentEvent")
              .bar(:class="{'bg-secondary': showRightContent === true}")
            longRightContent
    leftSideBar
    addNewClubModal(v-if="showNewClubModal")
    noticeModal(v-if="showNoticeModal ")
    messageModal(v-if="showMessageModal")
    settingModal(v-if="settingModal")
    smallPost(v-if="smallPost")
    smallChat(v-if="smallChat")
    showChat(v-if="showChatModal")

</template>

<script>
import { mapState } from 'vuex';
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue';
import rightContent from '@/components/rightContent.vue';
import longRightContent from '@/components/longRightContent.vue';
import leftContent from '@/components/leftContent.vue';
import leftSideBar from '@/components/leftSideBar.vue';
import addNewClubModal from '@/components/leftsidebar/addNewClubModal.vue';
import noticeModal from '@/components/leftsidebar/noticeModal.vue';
import messageModal from '@/components/leftsidebar/messageModal.vue';
import settingModal from '@/components/leftsidebar/settingModal.vue';
import smallPost from '@/components/smallPost.vue';
import smallChat from '@/components/smallChat.vue';
import showChat from '@/components/showChat.vue';


export default {
  name: 'Home',
  components: {
    rightContent,
    longRightContent,
    leftContent,
    leftSideBar,
    addNewClubModal,
    noticeModal,
    messageModal,
    settingModal,
    smallPost,
    smallChat,
    showChat,

  },
  computed: {
    ...mapState({
      showRightContent: (state) => state.showRightContent,
      showNewClubModal: (state) => state.showNewClubModal,
      showNoticeModal: (state) => state.showNoticeModal,
      showMessageModal: (state) => state.showMessageModal,
      sideBarRadio: (state) => state.sideBarRadio,
      settingModal: (state) => state.settingModal,
      showChatModal: (state) => state.showChatModal,
      smallPost: (state) => state.smallPost,
      smallChat: (state) => state.smallChat,
    }),
  },
  methods: {
    showRightContentEvent() {
      this.$store.commit('showRightContent');
      this.$store.state.smallPost = !this.$store.state.smallPost;
      this.$store.state.smallChat = !this.$store.state.smallChat;
    },
  },
  created() {
    this.$store.state.showRightContent = false;
  },
};
</script>

